@keyframes fadeIn {
    0% { opacity: 0 }
    100% { opacity: 1 }
  }

  @keyframes fadeOut {
    0% { opacity: 1 }
    100% { opacity: 0 }
  }

.Header {
    display: flex;
    justify-content: center;
    min-height: 100px;
    margin: 0;
    padding: 0;
}

.Crystalview {
    position: absolute;
    margin: 10px -10% 0;
    animation: fadeIn 5s normal;
    height: 10rem;
    width: auto;
  }
  
  .Faceview {
    position: absolute;
    margin: 10px -10% 0;
    height: 10rem;
    width: auto;
  }

  .Crystalview:hover {
    animation: fadeOut 5s, fadeIn 5s;
  }

  .Navbar {
    font-size: 20px;
    min-width: 300px;
    width: 20%;
    margin: 4em 0 0;
    padding: 0.5em 0 0.7em;
    display: flex;
    justify-content: space-evenly;
    border-top: double #2b2b2b;
    border-bottom: double #2b2b2b;
  }
  
